.header{animation:rightEntrance .5s ease-in-out}.header.open .header__menu--line{background-color:rgba(0,0,0,0)}.header.open .header__menu--line::before{transform:rotate(45deg)}.header.open .header__menu--line::after{transform:rotate(-45deg)}.header.open .header__menu--line::before,.header.open .header__menu--line::after{top:0}.header__overlay{width:100%;height:100vh;background-color:var(--color-tertiary);position:fixed;opacity:0;display:none;z-index:10}.header__nav{width:100%;height:7rem;line-height:7rem;display:flex;justify-content:space-evenly;background-color:var(--color-primary)}.header__nav i{margin:auto 0;font-size:3rem;color:var(--color-quaternary)}.header__nav__logo{padding:1rem}.header__nav__logo img{height:5rem;width:5rem}.header__nav--links{display:flex}.header__nav--links__items{position:relative}.header__nav--links__items a:link,.header__nav--links__items a:visited{font-size:2rem;color:#fff;font-weight:400;padding:0 3rem}.header__nav--links__items a:before,.header__nav--links__items a:after{position:absolute;opacity:0;top:0;font-size:calc(1.5rem + 1vw);font-weight:400;color:var(--color-quaternary);transition:all .3s}.header__nav--links__items a:before{content:"<";left:2rem}.header__nav--links__items a:after{content:">";right:2rem}.header__nav--links__items a:hover:before{opacity:1;transform:translateX(-1.5rem)}.header__nav--links__items a:hover:after{opacity:1;transform:translateX(1.5rem)}.header__nav--links__toggle-container{display:flex;align-items:center}.header__nav--links__toggle-container input[type=checkbox]{position:relative;width:7.5rem;height:3rem;margin:0 4rem;cursor:pointer;background-color:var(--color-quinary);border-radius:2.5rem;-webkit-appearance:none;-moz-appearance:none;transition:all .5s ease-in-out}.header__nav--links__toggle-container input:checked[type=checkbox]{background-color:var(--color-secondary-m)}.header__nav--links__toggle-container input:not(:checked)[type=checkbox]{background-color:var(--color-tertiary)}.header__nav--links__toggle-container input[type=checkbox]::before{position:absolute;width:2.8rem;height:100%;border-radius:50%;background-color:var(--color-quinary);transition:all .5s ease-in-out}.header__nav--links__toggle-container input[type=checkbox]::before{content:"";left:0;background-color:#fff}.header__nav--links__toggle-container input:checked[type=checkbox]::before{left:64%}.header__nav--links__toggle-container input:not(:checked)[type=checkbox]::after{right:1.5rem;top:.3rem;font-family:"Font Awesome 5 Free";font-weight:900;font-size:2rem;content:"";position:absolute;height:100%;color:var(--color-secondary)}.header__nav--links__toggle-container input:checked[type=checkbox]::after{left:1.3rem;top:.3rem;font-family:"Font Awesome 5 Free";font-weight:900;font-size:2rem;content:"";position:absolute;height:100%;color:#fff}@media screen and (min-width: 1300px){.header__nav--links__items a:link,.header__nav--links__items a:visited{font-size:2rem}}@media screen and (max-width: 800px){.header__nav{position:fixed;top:0;left:0;width:25rem;height:100vh;line-height:initial;flex-direction:column;text-align:center;z-index:20;transform:perspective(70rem) rotateY(0.5turn);transform-origin:top left;transition:transform .5s cubic-bezier(0.55, 0.055, 0.675, 0.19);animation:none}.header__nav--links{height:80%;flex-direction:column;justify-content:space-evenly}.header__nav--links__items{animation:initial}.header__nav--links__items a:link,.header__nav--links__items a:visited{transform-origin:left;margin-right:initial;padding:2rem 0;font-size:1.2rem}.header__nav--links__toggle-container{justify-content:center}.header.open nav{transform:rotateY(0)}.header__menu{height:4rem;width:4rem;position:fixed;top:2rem;right:2rem;padding:2.5rem;z-index:20;cursor:pointer}.header__menu--line,.header__menu--line::before,.header__menu--line::after{content:"";position:absolute;width:2.5rem;height:.2rem;background-color:var(--color-quinary);top:0;left:0;right:0;bottom:0;margin:auto;transition:all .5s ease-in-out}.header__menu--line::before{top:-2.2rem}.header__menu--line::after{top:1.3rem}.header__overlay.open{display:block;opacity:.8}}@media screen and (max-width: 480px){.header__nav{width:21rem}.header__nav--links__items a:link,.header__nav--links__items a:visited{font-size:1.5rem}}@keyframes rightEntrance{0%{opacity:0;transform:translateX(100%)}100%{opacity:1;transform:translateX(0)}}